 .card{
     width: 10rem;
     height: 13rem;
     position: absolute;
     display:  flex;
     
     flex-direction: column;
     gap: 1rem;
     align-items: center;
     width: 10rem;
     text-align: center;
     background: rgba(255,255,255,0.26);
     border: 7px solid var(--orangeCard);
     box-shadow: var(--boxShadow);
     border-radius: 20px;
     padding: 0px 26px 2rem 26px;
 }
 .card span:nth-of-type(2){
     color: var(--gray);
     font-size: 16px;
 }
 .card>img{
     transform: scale(0.6);
     margin-bottom: -2rem;
 }
 .c-button{
     background: #ffffff;
     box-shadow: 0px 19px 60px rgba(0,0,0,0.00);
     border-radius: 7px;
     border: none;
     padding: 10px;
     color: #5290FD;
 }
 .card #detail{   
     font-size:20px;
     margin-left: 10px;
 }
 @media screen and (max-width:480px) {
     .card{
         display: flex;
         justify-content: center;
         text-align: center;
         width: 11rem;
         /* margin-right: 20px; */
     }
     .card #detail{   
        font-size:20px;
        margin-left: 10px;
        color: darkorange;
    }
    .card-body{
        display: flex;
        justify-content: center;
        margin: auto;
    }
    
 }


