.floatingdiv{
    justify-content: center;
    display: flex;
    background: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center;
    padding: 0px 28px 0px 0px;
    height: 4.5rem;
    width: 8.5rem;
}
.floatingdiv>img{
    transform: scale(0.6);
}
.floatingdiv>span{
    font-family: sans-serif;
    font-size: 16px;
}
@media screen and (max-width:480px){
    .floatingdiv{
        margin-left: 100px;

    }
}